$bp-mobile: 27rem;

.movies-content {
  display: flex;
  margin-top: 1rem;
  flex-direction: column;

  &_nowPlaying {
    margin: 1.5rem 0rem 1rem;
    &--movie {
      border-radius: 0.5rem;
      background-size: cover;
      background-repeat: no-repeat;

      background-color: gray;
      position: relative;
      height: 50rem;

      @media only screen and (max-width: 80rem) {
        height: 40vh;
      }
      @media only screen and (max-width: 65rem) {
        height: 35vh;
      }

      @media only screen and (max-width: 37.5rem) {
        height: 25vh;
      }

      // @media only screen and (max-width: 32.5rem) {
      //   height: 20vh;

      // }

      // &-img {
      //   border-radius: .5rem;
      // }
      &-info {
        padding: 4rem 4rem 4rem;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;
        position: absolute;
        bottom: 0rem;
        background-image: linear-gradient(
          45deg,
          rgba(rgb(12, 11, 11), 0.8),
          rgba(rgb(12, 11, 11), 0)
        );
        width: 100%;
        height: 100%;
        &__section {
          color: rgb(168, 168, 168);
          font-size: 2rem;
          text-transform: capitalize;
        }
        &__title {
          color: white;
          font-size: 4rem;
          font-weight: 500;
          width: 60%;

          @media only screen and (max-width: 33rem) {
            font-size: 3rem;
          }
        }

        &__overview {
          margin-top: 1rem;
          width: 50%;
          color: rgb(177, 177, 177);
          font-size: 1.8rem;
          margin-bottom: 2rem;
          display: none;
        }

        &__rating {
          display: flex;
          & span {
            margin-right: 2rem;
          }
          margin-top: 0.3rem;
          width: 50%;
          color: #f32d58;
          font-size: 1.7rem;
          font-weight: 500;
          margin-bottom: 1rem;
        }
      }
    }
  }
  &_SecondarySection {
    width: 100%;
    margin-bottom: 1rem;

    // padding-bottom: 1rem;
    // height: 45rem;
    &-header {
      // background-color: green;
      display: flex;
      align-items: center;
      padding: 2rem 0rem 0;
      &--name {
        line-height: 1;
        z-index: 10;
        margin-right: 2rem;
        color: white;
        font-size: 2.6rem;
        font-weight: 500;
        text-transform: capitalize;
      }
    }

    &-movie {
      text-decoration: none;
      width: 100%;
      height: 100%;
      // display: flex;
      // justify-content: center;
      // align-items: center;
      position: relative;
      transition: all 0.2s;
      padding: 1.4rem 0.6rem;
      &--box {
        display: flex;
        justify-content: center;
        align-items: center;
        // height: 100%;

        // width: 20.5rem;
        &-img {
          border-radius: 0.4rem;
          width: 100%;
          height: 100%;

          // &:hover {
          //   box-shadow: 0rem 10rem 3rem rgba(0, 0, 0, 1);
          // }
        }
        position: relative;

        // background-color: rgba(27, 27, 27, 0.404);
        &-info {
          transition: all 0.3s;
          opacity: 0;
          position: absolute;
          margin: 0rem 0rem;
          bottom: 0rem;
          width: 100%;
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
          align-items: flex-start;
          // display: none;
          &--text {
            width: 100%;
            padding: 1rem 1rem 1.5rem;
            display: flex;
            justify-content: space-between;
            &-title {
              font-size: 1.7rem;
              color: white;
              width: 100%;
            }
            &-rating {
              color: #f32d58;
              font-size: 1.5rem;
            }
          }
        }
      }

      &:hover {
        transform: scale(1.05);
      }

      &--title {
        margin-top: 1rem;
        font-size: 1.6rem;
        // font-weight: 700;
        color: rgb(255, 255, 255);
      }
    }
  }
  &_genreSection {
    width: 100%;
    margin-bottom: 2rem;
    &-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 2rem 0.5rem 1rem;
      line-height: 1;

      &--name {
        color: white;
        font-size: 2.6rem;
        font-weight: 500;
        text-transform: capitalize;
      }
    }

    &-genre {
      // padding: 0.5rem 0.5rem;
      width: 100%;
      transition: all 0.2s;
      text-decoration: none;

      &:focus {
        outline: none;
      }
      &-box {
        width: 100%;
        padding: 3rem;
        height: 8rem;
        display: flex;

        // background-color: aquamarine;
        // flex-direction: column;
        justify-content: space-between;
        align-items: center;
        background-color: rgba(27, 27, 27, 0.404);
        border-radius: 0.4rem;
        font-size: 1.7rem;
        color: rgb(161, 161, 161);
        &--img {
          // flex: 2;
          // display: flex;
          // justify-content: center;
          // align-items: center;
          width: 7rem;
          // height: 10rem;
          // padding-bottom: 1rem;
          // margin-bottom: 1rem;
          // background-color: red;
          // width: 6rem;
          // flex: 1;
        }
        &--text {
          width: 100%;
          text-align: center;
        }
        &:hover {
          // border: .5rem solid #f32d58;
          background-color: #f32d583a;
        }
      }

      &:hover {
        transform: scale(1.05);
      }
    }
  }
  &_trailerSection {
    margin-bottom: 1rem;

    &-box {
      padding: 0 0.5rem;
    }
    // height: 46rem;

    & .slick-list > div {
      margin-left: 0;
    }
    // &-inner {
    //   //  width: 50%;
    //   // padding: ;
    // }
    &-header {
      display: flex;
      line-height: 1;
      // align-items: flex-end;
      justify-content: space-between;
      padding: 2rem 0.5rem 0;
      &--name {
        color: white;
        font-size: 2.6rem;
        font-weight: 500;
        text-transform: capitalize;
      }
    }
    // width: 50rem;
    &-trailer {
      margin-top: 1.3rem;
      overflow: hidden;
      // border:.2rem solid #000;
      z-index: 10;

      position: relative;
      padding-top: 56.25%; /* Player ratio: 100 / (1280 / 720) */
      border-radius: 0.5rem;
      &--player {
        // padding: 0 1rem;
        // width: 100%;
        // height: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  &_networkSection {
    // padding: 0 5rem;
    width: 100%;
    // margin-bottom: 1rem;
    &-header {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      padding: 2rem 0.5rem 0rem;
      &--name {
        line-height: 1;
        z-index: 10;
        color: white;
        font-size: 2.6rem;
        font-weight: 500;
        text-transform: capitalize;
      }
    }

    &-network {
      width: 100%;
      transition: all 0.2s;
      text-decoration: none;
      padding: 1.4rem 0.5rem;

      &:focus {
        outline: none;
      }
      &-box {
        // width: 15rem;
        height: 20rem;
        // padding: 2rem 9rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-image: linear-gradient(45deg, #c52144, #6b1313);
        border-radius: 0.5rem;
        font-size: 1.7rem;
        color: rgb(161, 161, 161);
        &--img {
          filter: brightness(0) invert(0);
          display: flex;
          justify-content: center;
          align-items: center;
          width: 45%;
          // padding-bottom: 1rem;
          // margin-bottom: 1rem;
          // background-color: red;
          // width: 6rem;
          // flex: 1;
        }
        &:hover {
          // border: .5rem solid #f32d58;
          background-color: #5545498e;
        }
      }

      &:hover {
        transform: scale(1.05);
      }
    }
  }
}

.genreWrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(24rem, 1fr));
  grid-gap: 1rem;
}
